<template>
  <div :class="[$style.navContainer, isShow ? $style.active : null]">
    <router-link :to="{ name: 'home' }">
      <div :class="$style.logo">
        b.
      </div>
    </router-link>
    <div :class="[$style.navSlider, isChanged ? $style.active : null]"></div>
    <div :class="$style.navIcon" @click="isShow = !isShow">
      <svg
        @click="isChanged = !isChanged"
        :class="isChanged ? $style.active : null"
        width="32"
        height="32"
      >
        <transition-group name="layout" tag="g">
          <rect :class="$style.line" ref="rect" key="rect1" width="15" height="2" />
          <rect :class="$style.line" ref="rect" key="rect2" x="0" y="6" width="30" height="2" />
          <rect :class="$style.line" ref="rect" key="rect3" x="15" y="12" width="15" height="2" />
        </transition-group>
      </svg>
    </div>
    <transition name="slide-in">
      <div v-if="isShow">
        <div :class="$style.nav">
          <div :class="$style.navContent">
            <span :class="$style.navItem" v-for="nav in navs" :key="nav.name">
              <router-link :to="{ name: nav.path }" :class="$style.navLink" @click.native="hideNav">{{ nav.name }}</router-link>
            </span>
          </div>
          <div :class="$style.navSocialIcons">
            <div v-for="social in socials" :key="social.title" :class="$style.socialItem">
              <a
                :href="social.url"
                :class="$style.socialLink"
                target="_blank"
                rel="noopener"
                :title="social.title"
              >
                <font-awesome-icon :icon="['fab', social.icon]" />
              </a>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div :class="$style.themeToggleContainer">
      <div v-if="theme == 'dark'">
        <svg
          @click="switchTheme"
          xmlns="http://www.w3.org/2000/svg"
          width="32"
          height="32"
          fill="none"
          stroke="white"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <circle cx="12" cy="12" r="5"></circle>
          <path d="M12 1v2M12 21v2M4.22 4.22l1.42 1.42M18.36 18.36l1.42 1.42M1 12h2M21 12h2M4.22 19.78l1.42-1.42M18.36 5.64l1.42-1.42"></path>
        </svg>
      </div>
      <div v-else>
        <svg
          @click="switchTheme"
          xmlns="http://www.w3.org/2000/svg"
          width="32"
          height="32"
          fill="none"
          stroke="white"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path>
        </svg>
      </div>
    </div>
  </div>
</template>

<style lang="scss" module>
  @import './Menu.module';
</style>

<script>
import { library } from '@fortawesome/fontawesome-svg-core';
import {
  faLinkedin,
  faGithub
} from '@fortawesome/free-brands-svg-icons';

library.add(faLinkedin, faGithub);

export default {
  data() {
    return {
      navs: [
        { path: 'home', name: 'Home' },
        { path: 'projects', name: 'Projects' },
        { path: 'blog', name: 'Blog' },
        { path: 'about', name: 'About' }
      ],
      socials: [
        { title: 'Github', icon: 'github', url: 'https://www.github.com/bonabrian' },
        { title: 'Linkedin', icon: 'linkedin', url: 'https://www.linkedin.com/in/bonabrian/' },
      ],
      theme: localStorage.getItem('theme') ? localStorage.getItem('theme') : process.env.VUE_APP_DEFAULT_THEME,
      isShow: false,
      isChanged: false
    };
  },
  methods: {
    hideNav() {
      this.isShow = false;
      this.isChanged = false;
    },
    switchTheme() {
      let bodyElement = document.body;
      if (this.theme == 'dark') {
        this.theme = 'light';
        localStorage.setItem('theme', 'light');
        bodyElement.setAttribute('theme', 'light');
      } else {
        this.theme = 'dark';
        localStorage.setItem('theme', 'dark');
        bodyElement.setAttribute('theme', 'dark');
      }
    }
  }
}
</script>