import Vue from 'vue';
import Router from 'vue-router';
import Meta from 'vue-meta';

// Layouts
import LayoutApp from './layouts/LayoutApp.vue';

// Home
import Home from './pages/Home/Home.vue';

// About
import About from './pages/About/About.vue';

// Blog
import Blog from './pages/Blog/Blog.vue';

// Projects
import Projects from './pages/Projects/Projects.vue';
import Brivia from './pages/Projects/Brivia/Brivia.vue';
import Wikas from './pages/Projects/Wikas/Wikas.vue';
import Nazar from './pages/Projects/Nazar/Nazar.vue';
import ShootingSport from './pages/Projects/ShootingSport/ShootingSport.vue';
import Varnion from './pages/Projects/Varnion/Varnion.vue';
import Career from './pages/Projects/Career/Career.vue';
import Coretan from './pages/Projects/Coretan/Coretan.vue';
import Metrillo from './pages/Projects/Metrillo/Metrillo.vue';

import NotFoundPage from './pages/404/404.vue';

Vue.use(Router);
Vue.use(Meta);

const scrollBehavior = (to, from, savedPosition) => {
  let position = {};
  if (savedPosition) {
    position = savedPosition;
  }
  if (to.matched.some(m => m.meta.scrollTop)) {
    position.x = 0;
    position.y = 0;
  }
  return position;
};

const routes = [
  {
    path: '/',
    base: process.env.VUE_APP_BASE_URL,
    component: LayoutApp,
    children: [
      {
        path: '/',
        name: 'home',
        component: Home,
        meta: { scrollTop: true }
      },
      {
        path: '/about',
        name: 'about',
        component: About,
        meta: { scrollTop: true }
      },
      {
        path: '/blog',
        name: 'blog',
        component: Blog,
        meta: { scrollTop: true }
      },
      {
        path: '/projects',
        component: LayoutApp,
        meta: { scrollTop: true },
        children: [
          {
            path: '/',
            name: 'projects',
            component: Projects,
          },
          {
            path: '/projects/wikas',
            name: 'wikas',
            component: Wikas
          },
          {
            path: '/projects/shootingsport',
            name: 'shootingsport',
            component: ShootingSport
          },
          {
            path: '/projects/coretan',
            name: 'coretan',
            component: Coretan
          },
          {
            path: '/projects/nazar',
            name: 'nazar',
            component: Nazar
          },
          {
            path: '/projects/brivia',
            name: 'brivia',
            component: Brivia,
          },
          {
            path: '/projects/varnion',
            name: 'varnion',
            component: Varnion
          },
          {
            path: '/projects/varnioncareer',
            name: 'varnioncareer',
            component: Career
          },
          {
            path: '/projecs/metrillo',
            name: 'metrillo',
            component: Metrillo
          }
        ],
      },
    ]
  },
  { path: '*', component: NotFoundPage }
];

const router = new Router({
  routes,
  mode: 'history',
  scrollBehavior,
  linkActiveClass: 'active'
});

export default router;